import Vue from 'vue';
import VueRouter from 'vue-router';
import vueResource from 'vue-resource';

//
import App from './App';
import home from './views/home.vue';
import about from './views/about.vue';

Vue.use(VueRouter);
Vue.use(vueResource);

// debug
Vue.config.debug = true;
//  vue-resource  content-type urlencode ajax
Vue.http.options.emulateJSON = true

//2.0 router
const router = new VueRouter({
  // html5 history
  mode: 'history',
  routes: [
    //  
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      // , 
      path: '*',
      redirect: '/home'
    }
  ]
});

//
export default new Vue({
  el: '#app',
  router,
  render: h => h(App)
});



